export default {
	data() {
		return {}
	},
	onLoad() {

	},
	methods: {
		checkIsSupportSoterAuthentication() {
			uni.checkIsSupportSoterAuthentication({
				success(res) {
					console.log(res);
				},
				fail(err) {
					console.log(err);
				},
				complete(res) {
					console.log(res);
				}
			})
		},
		checkIsSoterEnrolledInDeviceFingerPrint() {
			uni.checkIsSoterEnrolledInDevice({
				checkAuthMode: 'fingerPrint',
				success(res) {
					console.log(res);
				},
				fail(err) {
					console.log(err);
				},
				complete(res) {
					console.log(res);
				}
			})
		},
		checkIsSoterEnrolledInDeviceFaceID() {
			uni.checkIsSoterEnrolledInDevice({
				checkAuthMode: 'facial',
				success(res) {
					console.log(res);
				},
				fail(err) {
					console.log(err);
				},
				complete(res) {
					console.log(res);
				}
			})
		},
		startSoterAuthenticationFingerPrint() {
			uni.startSoterAuthentication({
				requestAuthModes: ['fingerPrint'],
				challenge: '123456',
				authContent: '请用指纹解锁',
				success(res) {
					console.log(JSON.parse(res.resultJSON));
				},
				fail(err) {
					console.log(err);
				},
				complete(res) {
					console.log(res);
				}
			})
		},
		startSoterAuthenticationFaceID() {
			uni.startSoterAuthentication({
				requestAuthModes: ['facial'],
				challenge: '123456',
				authContent: '请用FaceID解锁',
				success(res) {
					console.log(res);
				},
				fail(err) {
					console.log(err);
				},
				complete(res) {
					console.log(res);
				}
			})
		}
	},
	onShow() {

		wx.startHCE({
			aid_list: ['F222222222'],
			success(res) {
				console.log(res.errMsg)
			},
			fail(res) {
				console.log(res)
			}
		})











	}
}
